<template>
<button
  :class="{
    [$c('button')]: true,
    [$c('button-loading')]: loading,
    [$c('disabled')]: disabled,
    [$c('button-icon-only')]: iconOnly
  }"
  :ui="`${realUi} ${uiParts.self}`"
  v-bind="attrs"
  v-on="$listeners"
>
  <veui-icon
    v-if="loading"
    :class="$c('button-loading-icon')"
    :name="icons.loading"
    spin
  />
  <slot/>
</button>
</template>

<script>
import 'veui/locale/zh-Hans/common.js'
import 'veui/locale/en-US/common.js'
import 'veui-theme-blue/components/Button.js'

import { omit } from 'lodash'
import Icon from './Icon'
import prefix from '../mixins/prefix'
import ui from '../mixins/ui'
import focusable from '../mixins/focusable'
import { hasClass as vnodeHasClass } from '../utils/helper'

export default {
  name: 'veui-button',
  components: {
    'veui-icon': Icon
  },
  mixins: [prefix, ui, focusable],
  props: {
    disabled: Boolean,
    name: String,
    type: {
      type: String,
      default: 'button'
    },
    value: String,
    loading: Boolean
  },
  data () {
    return {
      iconOnly: false
    }
  },
  computed: {
    attrs () {
      let props = omit(this.$props, 'loading')
      props.disabled = this.disabled || this.loading
      return props
    }
  },
  created () {
    this.checkIcon()
  },
  updated () {
    this.checkIcon()
  },
  methods: {
    checkIcon () {
      // Temporary hack until we can find a pure CSS solution
      let content = (this.$slots.default || []).filter(
        vnode =>
          (vnode.tag || vnode.text) &&
          !vnodeHasClass(vnode, this.$c('check-button-group-checkmark'))
      )
      if (content) {
        let vnode = content[0]
        this.iconOnly =
          content.length === 1 &&
          ((vnode.componentOptions &&
            vnode.componentOptions.Ctor.options.name === 'veui-icon') ||
            (vnode.data &&
              vnode.data.class &&
              vnode.data.class.indexOf('dls-icon') !== -1))
      }
    },
    focus () {
      this.$el.focus()
    }
  }
}
</script>
<style lang="less" src="veui-theme-blue/components/button.less"></style>
